
@import '~antd/dist/antd.css';

.App {
  text-align: center;
  padding: 5%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.customAdd {
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.header {
  display: flex;
}
.header .placeholder {
  flex: 1;
}

.ant-input-affix-wrapper {
  flex: 5;
}

.ant-select {
  flex: 4;
}

.add-select {
  flex: 1;
}

.add-select i {
  margin-top: 5px;
}
.ant-btn {
  flex: 1;
}
